// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import axios from 'axios'
import Qs from 'qs'
import Mock from './mock/mock.js'
import ElementUI from 'element-ui'
import 'element-ui/lib/theme-chalk/index.css'
import CryptoJS from 'crypto-js'



// 开发环境中开启mock服务
if(process.env.NODE_ENV == 'development'){
	// Mock.bootstrap();
}

Vue.prototype.$http = axios; //直接不能用axios，而是将axios注册给vue的属性

Date.prototype.format = function(format) {
	var date = {
		"M+": this.getMonth() + 1,
		"d+": this.getDate(),
		"h+": this.getHours(),
		"m+": this.getMinutes(),
		"s+": this.getSeconds(),
		"q+": Math.floor((this.getMonth() + 3) / 3),
		"S+": this.getMilliseconds()
	};
	if (/(y+)/i.test(format)) {
	      format = format.replace(RegExp.$1, (this.getFullYear() + '').substr(4 - RegExp.$1.length));
	}
	for (var k in date) {
	      if (new RegExp("(" + k + ")").test(format)) {
	             format = format.replace(RegExp.$1, RegExp.$1.length == 1
	                    ? date[k] : ("00" + date[k]).substr(("" + date[k]).length));
	      }
	}
	return format;
}
Vue.filter('time', function (value) {
    let newDate = new Date(parseInt(value) * 1000);
    return newDate.format('yyyy-MM-dd h:m:s');
})
Vue.filter('timeDate', function (value) {
	let newDate = new Date(parseInt(value) * 1000);
    return newDate.format('yyyy-MM-dd');
})

Vue.use(ElementUI);//运用饿了吗的ui



// 直接访问login ，清除登录状态
// 如果不是则判断是否存在登录状态 然后 next()
router.beforeEach((to, from, next) => {
  if (to.path == '/login') {
    localStorage.removeItem('user');
  }
  let user = JSON.parse(localStorage.getItem('user'));
  if (!user && to.path != '/login') {
    next({ path: '/login' })
  } else {
    next()
  }
})

/**
 * 引入全局静态资源
 */
import './assets/scss/normalize.scss'
import './assets/scss/reset.scss'
import './assets/scss/public.scss'

Vue.config.productionTip = false

/* eslint-disable no-new */
let mcVue = new Vue({
  el: '#app',
  router,
  template: '<App/>',
  components: { App },
  created() {
  	axiosFilter();
  }
})



/**
 * 获取token
 * @return {[string]} [返回token字符串]
 */
function getToken() { 
	return JSON.parse(localStorage.getItem('user'))['X-Token'] || '';
};


/**
 * AES加密
 * @return {[string]} [返回加密后字符串]
 */
function encrypt(word){
	let key = 'eIa@yb^vpG4Kovtr';
	key = CryptoJS.enc.Utf8.parse(key);
	let iv = "eIa@yb^vpG4Kovtr";
	iv = CryptoJS.enc.Utf8.parse(iv);
	let encrypted = CryptoJS.AES.encrypt(word, key, {
		iv: iv,
		mode: CryptoJS.mode.CBC,
		padding: CryptoJS.pad.Pkcs7 
	}).toString();
	return encrypted;
}

/**
 * axios过滤器
 */
function axiosFilter() {
	axios.interceptors.request.use(
		config => {
			let xtoken = config.url.indexOf('doLogin') > -1 ? '' : getToken();
			config.headers['Content-Type'] = 'application/x-www-form-urlencoded';
			
			if(xtoken != null){
				config.headers['X-Token'] = xtoken;
			}
			//login  加密开始
			if( config.url.indexOf('doLogin') > -1){
				//let str=encrypt(JSON.stringify(config.data));
				let str=JSON.stringify(config.data);
				config.data={}
				config.data.data=str;
			}
			//login  加密结束
			if(config.method=='post'){
				config.data = {
					...config.data,
					_t: Date.parse(new Date())/1000,
				}
				config.data = Qs.stringify(config.data);
			}else if(config.method=='get'){
				config.params = {
					_t: Date.parse(new Date())/1000,
					...config.params
				}
			}
			return config
		},function(error){
			return Promise.reject(error)
		}
	)
	axios.interceptors.response.use(function (response) {
		// token 已过期，重定向到登录页面
		if (response.data.code == 200000401){
			mcVue.$message.error('登录已过期，请重新登录');
			localStorage.clear();
			router.replace({
		        path: '/login',
		        query: {redirect: router.currentRoute.fullPath}
		    })
		}else if(response.data.code != 0 && response.data.code != 200000401){
			mcVue.$message.error(response.data.msg);
		}
		return response;
	}, function (error) {
		// Do something with response error
		return Promise.reject(error)
	})
}








