import Vue from 'vue'
import Router from 'vue-router'
// 登录
const login = resolve => require(['@/views/login'], resolve)
// 主页面
const home = resolve => require(['@/views/home'], resolve)
// 404
const notFound = resolve => require(['@/views/404'], resolve)
// 修改密码
const password = resolve => require(['@/views/password'], resolve)
// 总览
const index = resolve => require(['@/views/index'], resolve)

//品牌车型管理
const carTypeList = resolve => require(['@/views/carType/index'], resolve)
//知识库数据类型管理
const knowledge = resolve => require(['@/views/carType/knowledgeView'], resolve)
//知识管理
const knowledgeAdd = resolve => require(['@/views/carType/knowledgeManage'], resolve)
//公司列表
const companyList = resolve => require(['@/views/company/index'], resolve)
//公司管理员列表
const companyAdminList = resolve => require(['@/views/company/companyAdmin'], resolve)
//页面列表
const pageLists = resolve => require(['@/views/company/pageList'], resolve)
//工作流角色列表
const roleLists = resolve => require(['@/views/company/roleList'], resolve)
Vue.use(Router)

export default new Router({
  routes: [
	{
		path: '/login',
		component: login,
		name: '',
		hidden: true
	},
    {
		path: '/404',
		name: '404',
		component: notFound,
		hidden: true
    },
    {
        path: '/',
        component: home,
        name: '',
        iconCls: 'el-icon-message',
        hidden: true,
        leaf: true,//只有一个节点
        children: [
            { path: '/password', component: password, name: '修改密码'}
        ]
    },
    // {
    //     path: '/',
    //     component: home,
    //     name: '',
    //     iconCls: 'el-icon-message',
    //     leaf: true,//只有一个节点
    //     children: [
    //         { path: '/index', component: index, name: '首页',hidden:true }
    //     ]
    // },
    {
        path: '/',
        component: home,
        name: '资源管理',
        iconCls: 'el-icon-news',//图标样式class
        children: [
            {path: '/carTypeList', component: carTypeList, name: '品牌车型管理'},
            {path: '/knowledge', component: knowledge, name: '知识库数据类型管理'},
            {path: '/knowledgeAdd', component: knowledgeAdd, name: '知识库管理'},
        ]
    },
    {
        path: '/',
        component: home,
        name: '租户管理',
        iconCls: 'el-icon-printer',//图标样式class companyAdminList roleLists
        children: [
            {path: '/companyList', component: companyList, name: '公司列表'},
            {path: '/companyAdminList', component: companyAdminList, name: '公司管理员管理'},
            {path: '/pageLists', component: pageLists, name: '页面管理'},
            {path: '/roleLists', component: roleLists, name: '工作流角色管理'},
        ]
    },
    {
        path: '*',
        hidden: true,
        redirect: { path: '/404' }
    }
  ]
})



