import Mock from 'mockjs';
import axios from 'axios';
import MockAdapter from 'axios-mock-adapter';
import { LoginUsers, movieList } from './data/data';
let _movieList = movieList;
export default {
  /**
   * mock bootstrap
   */
  bootstrap() {
    let mock = new MockAdapter(axios);

    // mock success request
    mock.onGet('api/success').reply(200, {
      msg: 'success'
    });

    // mock error request
    mock.onGet('api/error').reply(500, {
      msg: 'failure'
    });

    //登录
    mock.onPost('api/login').reply(config => {
      let {username, password} = JSON.parse(config.data);
      return new Promise((resolve, reject) => {
        let user = null;
        setTimeout(() => {
          let hasUser = LoginUsers.some(u => {
            if (u.username === username && u.password === password) {
              user = JSON.parse(JSON.stringify(u));
              user.password = undefined;
              return true;
            }
          });

          if (hasUser) {
            resolve([200, { status: 200, msg: '请求成功', user}]);
          } else {
            resolve([200, { status: 500, msg: '账号或密码错误' }]);
          }
        }, 1000);
      });
    });

    //获取影片列表（分页）
    mock.onGet('/api/getmovielist/wait').reply(config => {
      let {page, value} = config.params;
      let mockUsers = _movieList.filter(user => {
        if (value && user.videoName.indexOf(value) == -1) return false;
        return true;
      });
      let total = mockUsers.length;
      mockUsers = mockUsers.filter((u, index) => index < 10 * page && index >= 10 * (page - 1));
      return new Promise((resolve, reject) => {
        setTimeout(() => {
          resolve([200, {
            status:200,
            total: total,
            data: mockUsers
          }]);
        }, 1000);
      });
    });
    //获取影片列表（分页）
    mock.onGet('/api/getmovielist/online').reply(config => {
      let {page, value} = config.params;
      let mockUsers = _movieList.filter(user => {
        if (value && user.videoName.indexOf(value) == -1) return false;
        return true;
      });
      let total = mockUsers.length;
      mockUsers = mockUsers.filter((u, index) => index < 10 * page && index >= 10 * (page - 1));
      return new Promise((resolve, reject) => {
        setTimeout(() => {
          resolve([200, {
            total: total,
            data: mockUsers
          }]);
        }, 1000);
      });
    });
    //获取影片列表（分页）
    mock.onGet('/api/getmovielist/offline').reply(config => {
      let {page, value} = config.params;
      let mockUsers = _movieList.filter(user => {
        if (value && user.videoName.indexOf(value) == -1) return false;
        return true;
      });
      let total = mockUsers.length;
      mockUsers = mockUsers.filter((u, index) => index < 10 * page && index >= 10 * (page - 1));
      return new Promise((resolve, reject) => {
        setTimeout(() => {
          resolve([200, {
            total: total,
            data: mockUsers
          }]);
        }, 1000);
      });
    });


    
  }
};


