import Mock from 'mockjs';
const LoginUsers = [
  {
    id: 1,
    username: 'admin',
    password: '123456',
    avatar: 'https://raw.githubusercontent.com/taylorchen709/markdown-images/master/vueadmin/user.png',
    name: '张某某'
  }
];


const movieList = [];

for (let i = 0; i < 35; i++) {
  movieList.push(Mock.mock({
    id: Mock.Random.guid(), // 电影id
    movieName: Mock.Random.cname(), // 影片名
    movieMoney: Mock.mock('@natural(60, 100)'), // 价格
    movieTime: Mock.mock('@natural(60, 100)'), // 时长
    movieGrade: Mock.mock('@natural(0, 10)'), // 评分
    movieType: Mock.Random.cname(), // 类型
    movieCountry: Mock.Random.cname(), // 国家
    movieDirector: Mock.Random.cname(), // 导演
    movieActor: Mock.Random.cname(), // 演员
    moviePrevue: Mock.Random.url(), // 预告片url
    movieMovie: Mock.Random.url() // 影片url
  }));
}

export { LoginUsers, movieList};


